import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { authServerHost, resourceHost } from './variables.js'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        // 로그인 사용자 정보
        principal: null,
    },
    getters: {
        getPrincipal: state => state.principal,
    },
    mutations: { // commit
        // // 로그인이 성공하면 state.authenticated = true 로 설정한다.
        // loginSuccess(state, payload) {
        //     console.log('loginSuccess', state, payload);
        // },
        // 가져온 사용자 정보를 설정한다. main.js getUserInfo 호출후 결과 반영.
        getUserInfoSuccess(state, payload) {
            state.principal = payload.principal;
        },
        logoutSuccess(state) {
            state.principal = null;
        }
    },
    actions: { // dispatch
        login({ dispatch }, { loginId, loginPw }) {
            var params = new URLSearchParams();
            params.append('username', loginId);
            params.append('password', loginPw);
            params.append('grant_type', 'password');

            const config = {
                url: authServerHost + '/oauth/token',
                method: 'POST',
                headers: { Authorization: 'Basic Y2xpZW50MTpjbGllbnQxcHdk' },
                data: params,
            };
            axios(config).then(res => {
                sessionStorage.setItem('authenticated', true)
                sessionStorage.setItem('token_type', res.data.token_type)
                sessionStorage.setItem('access_token', res.data.access_token)
                sessionStorage.setItem('refresh_token', res.data.refresh_token)
                axios.defaults.headers.common['Authorization'] = res.data.token_type + ' ' + res.data.access_token;
                dispatch('getUserInfo');
            }).catch(err => {
                console.error('error : ', err);
            });
        },
        logout({ commit }) {
            sessionStorage.removeItem('authenticated')
            axios.defaults.headers.common['Authorization'] = undefined
            sessionStorage.removeItem('token_type')
            sessionStorage.removeItem('access_token')
            sessionStorage.removeItem('refresh_token')
            commit('logoutSuccess')
        },
        getUserInfo({ commit }) {
            const config = {
                url: resourceHost + '/hs/userInfo',
                method: 'GET',
            };
            // console.log('store.getUserInfo :: :: axios.defaults.headers : ', axios.defaults.headers);
            axios(config).then(res => {
                commit('getUserInfoSuccess', res.data);
            }).catch(err => {
                console.error('store.getUserInfo :: err: ', err);
            });
        },
    },
    modules: {
    },

})
