import Vue from 'vue'
import VueRouter from 'vue-router'
import axios from 'axios'
import Main from '../view/Main.vue'
import Login from '../view/Login.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        component: Main,
        meta: {requiresAuth: true},
        children: [
            { path: '/logs/logs01List', component: () => import(/* webpackChunkName: "logs01List" */ '../view/logs/Logs01List.vue') },
            { path: '/logs/logs02List', component: () => import(/* webpackChunkName: "logs02List" */ '../view/logs/Logs02List.vue') },
            { path: '/logs/logs03List', component: () => import(/* webpackChunkName: "logs03List" */ '../view/logs/Logs03List.vue') },
            { path: '/logs/logs04List', component: () => import(/* webpackChunkName: "logs04List" */ '../view/logs/Logs04List.vue') },
            { path: '/logs/logs05List', component: () => import(/* webpackChunkName: "logs05List" */ '../view/logs/Logs05List.vue') },
            { path: '/logs/logs06List', component: () => import(/* webpackChunkName: "logs06List" */ '../view/logs/Logs06List.vue') },
            { path: '/logs/logs07List', component: () => import(/* webpackChunkName: "logs07List" */ '../view/logs/Logs07List.vue') },
        ],
    },
    {
        path: '/login',
        name: 'login',
        component: Login,
    },
    { path: '*', component: Login },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
      if (!sessionStorage.getItem('authenticated')) {
        next({ path: 'login?returnPath=' + from.path })
      } else {
        axios.defaults.headers.common['Authorization'] = sessionStorage.getItem('token_type') + ' ' + sessionStorage.getItem('access_token');
        next()
      }
    } else {
      next()
    }
  })

export default router
