<template>
  <v-app id="inspire">
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <form @submit.prevent="onSubmit()">
              <v-card class="elevation-12">
                <v-toolbar color="primary" dark flat>
                  <v-toolbar-title>System Login</v-toolbar-title>
                  <v-spacer />
                </v-toolbar>
                <v-card-text>
                  <v-text-field
                    label="Login"
                    name="loginId"
                    v-model="loginId"
                    prepend-icon="mdi-account"
                    type="text"
                    autocomplete="username"
                  />

                  <v-text-field
                    label="Password"
                    name="loginPw"
                    v-model="loginPw"
                    prepend-icon="mdi-lock"
                    type="password"
                    autocomplete="current-password"
                  />
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn type="submit" color="primary">Login</v-btn>
                </v-card-actions>
              </v-card>
            </form>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      loginId: "",
      loginPw: "",
      msg: ""
    };
  }, // data
  methods: {
    onSubmit() {
      this.$store
        .dispatch("login", {
          loginId: this.loginId,
          loginPw: this.loginPw
        })
        .then(() => {
          let returnPath = new URL(location.href).searchParams.get("returnPath");
          console.log('returnPath: ', returnPath);
          if (returnPath === '/login') {
            returnPath = '/';
          }
          setTimeout(() => {
            console.log('setTimeout');
            this.$router.push(returnPath).catch(err => {
              console.error("error", err);
            });            
          }, 1500);
        })
        .catch(message => {
          this.msg = message;
        });
    }, // onSubmit
  }, // methods
};
</script>

